.join{
    .inner{
        >h1{
            a{
                font: normal 80px/1 "PT Sans Narrow";
                color: #333;
            }       

            &::after{
                content: "";
                display: block;
                width: 68%;
                height: 1px;
                background: #555;
                margin-top: 20px;
                margin-bottom: 30px;
            }
        }
		>p{
            font: 16px/1.3 "PT Sans Narrow";
            color: #555;
            margin: 0 auto;
            width: 450px;
            word-wrap: break-word;
            letter-spacing: 1px;
            margin-bottom: 150px;
        }
		h2{
			font: bold 25px/1 "PT Sans Narrow";
			color: #333;
			letter-spacing: 0.5px;
			margin-bottom: 20px;
		}
		.agreement{
			font: normal 14px/1.3 "arial";
			color: #777;
			padding: 10px 20px;
			width: 100%; height: 200px;
			margin-bottom: 20px;
			resize: none;
			border: 1px solid #aaa;
		}
		.agree{
			// width: 70%;
			// text-align: center;
			margin-bottom: 100px;
			font: normal 14px/1 "arial";
			color: #333;
			input[type="checkbox"] {
				margin-right: 10px;
			}
			
		}
        table {
			float: left;
			width: 70%;
			border-top: 1px solid #333;
			border-bottom: 1px solid #333;


			th,
			td {
				// border-bottom: 1px solid #ddd;
				padding: 10px 30px;
			}
			th {
				width: 20%;
				// border-right: 1px solid #ddd;
				text-align: left;
				font: bold 14px/1 "arial";
				color: #555;

				&.btnSet {
					text-align: center;
					border-right: none;

					input[type="reset"],
					input[type="submit"] {
						width: 150px;
						height: 32px;
						border: 1px solid #555;
						text-align: center;
						margin: 10px;
						cursor: pointer;
						transition: 0.5s;

						&:hover {
							background: #555;
							color: #fff;
						}
					}
				}
			}
			td {
				input[type="text"],
				input[type="password"],
				select {
					width: 100%;
					padding: 10px 20px;
					border: 1px solid #aaa;
					color: #555;
					// box-shadow: inset 3px 3px 10px rgba(0,0,0,.1);
				}
				textarea {
					width: 100%;
					padding: 10px 20px;
					border: 1px solid #aaa;
					height: 150px;
					resize: none;
				}
				label {
					font: 12px/1 "arial";
					color: #777;
					margin-right: 5px;
				}
				input[type="radio"],
				input[type="checkbox"] {
					margin-right: 20px;
				}
			}
		}

		.join_r{
			float: right;
			width: 25%;

			.join_box{
				
				border-radius: 8px;
				cursor: pointer;
				width: 100%;
				height: 192px;
				margin-bottom: 20px;
				background: #333;
				position: relative;
				transition: 1s;
				background: url(../../public/img/join1.jpg)no-repeat;
				background-position: center;
				background-size: cover;

				&:nth-child(2){
					background: url(../../public/img/join4.jpg)no-repeat;
					background-position: center;
					background-size: cover;
				}

				>h1{
					width: 100%;
					position: absolute;
					top: 50%; left: 50%;
					transform: translate(-50%, -50%);
					font: bold 18px/1.2 "PT Sans Narrow";
					color: #fff;
					letter-spacing: 1px;
					text-align: center;
				}

				&:last-child{
					background: url(../../public/img/join5.jpg)no-repeat;
					background-position:top;
					background-size: cover;
					margin-bottom: 0;
					
				}

				&:hover{
					transform: scale(0.98);
				
				}
			}
		}
		.err {
			font: 11px/1 "맑은고딕";
			color: rgba(192, 1, 1, 0.836);
		}
    }
}